<template>
  <div class="bg">
    <div style="width:390px;margin-top:9%;margin-left:35.5%;">
      <p>排&nbsp;行&nbsp;榜</p>
      <el-table
        :data="ranginglist.slice((currentPage-1)*pageSize,currentPage*pageSize)"
        height="330"
        border
        style="width: 700;"
      >
        <el-table-column prop="player_id" label="玩家id"></el-table-column>
        <el-table-column prop="name" label="用户名"></el-table-column>
        <el-table-column prop="score" label="分数"></el-table-column>
      </el-table>
      <el-pagination
        style="margin-top:0%;margin-left:0%"
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="ranginglist.length"
        @current-change="handleCurrentChange"
      ></el-pagination>
      <div class="buttons" style="margin-top:-100px;margin-left:590px;">
        <img src="../assets/history.png" alt @click="skiphistory" />
        <img src="../assets/BeginnewGame.png" alt @click="skipplay" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ranking',
  data() {
     return {
       currentPage: 1,
        pageSize:6,
        ranginglist:[],
        squareUrl: "https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png",
        backDialog: false
      }
    },
    created() {
      this.getranking()
    },

    methods: {
      skipplay(){
          this.$router.push({path:'/play'});
      },
      skiphistory(){
          this.$router.push({path:'/history'});
      },
       handleCurrentChange(currentPage) {
      this.currentPage = currentPage
    },
      getranking(){
         let _this=this;
        console.log(_this.$store.state.UserId)
        axios.get('https://api.shisanshui.rtxux.xyz/rank/rank.json', {
           
        })
        .then(function (response) {
       
          console.log(response.data)
          _this.ranginglist=response.data
           
        })
        .catch(function (error) {
          console.log(error);
        })
      }
   
    }
  };
</script>

<style lang="scss" scoped>
.bg {
  position: absolute;
  width: 100%;
  height: 100%;

  background: url("../assets/RankListbg.jpg");
  background-size: contain;
  background-repeat: no-repeat;
  -ms-interpolation-mode: bicubic;
  background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  p {
    text-align: center;
    font-family: "隶书";
    font-size: 30px;
    margin-block-end: 10px;
    margin-bottom: 10px;
  }
  .buttons {
    img {
      width: 197px;
      height: 80px;
      cursor: pointer;
    }
  }
  tr {
    text-align: center;
    background: rgba(0, 0, 0, 0);
  }

  td {
    height: 60px;
    font-family: "Cooper", "Helvetica Neue", Helvetica, "PingFang SC",
      "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    font-size: 20px;
  }
}
</style>



