
<template>
  <div class="loginbg1" >
    <span>
  <img src="../assets/PrepareGame.png" alt="" @click="skipplay" style="margin-top:38%;margin-left:35%;">    
    

  <img src="../assets/history.png" alt="" @click="skipranking" style="margin-top:38%;margin-left:3%;">
   </span> 
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      
        
      };
    },
    methods: {
      skipplay(){
          this.$router.push({path:'/play'});
      },
      skipranking(){
          this.$router.push({path:'/history'});
      }
       
    }
  }
</script>

<style lang="scss" scoped>
.loginbg1 {
  
  position:absolute;
  width: 100%;
  height:100%;
 
  background: url("../assets/bg.jpg");
  background-size: contain;
 background-repeat: no-repeat;
   -ms-interpolation-mode: bicubic;
   background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
 
  span{ 
    cursor : pointer;
}
}

</style>



